export default "import DetailOrderState from \"../../../../../packages/ui/src/molecules/DetailOrderState\";\nimport { Box, Stack, Typography } from \"@mui/material\";\nimport {\n  NavBar,\n  CartIcon,\n  MainLogo,\n  StepStatus,\n  Button,\n} from \"../../../../../packages/ui/src\";\n\nexport default function OrderState() {\n  return (\n    <Stack\n      direction={\"column\"}\n      justifyContent={\"space-between\"}\n      alignItems={\"center\"}\n      sx={{ height: \"70rem\" }}\n    >\n      <NavBar\n        cartComponent={<CartIcon />}\n        mainLogo={<MainLogo />}\n        searchBar={<Button label=\"Buscar\" variant=\"contained\" />}\n      />\n      <Typography variant=\"h2\" sx={{ margin: \" 0 auto\", border: \"none\" }}>\n        Estado de pedido\n      </Typography>\n      <StepStatus steps={[\"En tienda\", \"En camino\", \"Entregado\"]} />\n      <DetailOrderState />\n      <Box\n        display={{ xs: \"none\", sm: \"flex\" }}\n        justifyContent=\"center\"\n        sx={{ width: \"100%\" }}\n      >\n        <MainLogo />\n      </Box>\n    </Stack>\n  );\n}\n"